@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Manrope", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.link-underlined {
  text-decoration: underline;
}

.center-column {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.comments {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 2px 16px;
  margin: 5px 10px;
}

.producer {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 5px 10px;
  height: 300px;
  max-width: 250px;
  margin-bottom: 2rem;
  border: 0;
  flex-basis: 250px;
  flex-grow: 0;
  flex-shrink: 0;
  border-radius: 4%;
}

.scrolling-wrapper {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  margin-top: 10px;
}

.scrolling-wrapper::-webkit-scrollbar {
  display: none;
}

footer {
  background-color: rgb(68, 68, 68);
  color: white;
  width: 100%;
  margin-top: 10px;
}
